.landing {
    position: relative;
}

.hero {
    height: 100vh;
    position: relative;
}

.hero-image-wrapper {
    height: 100%;
    position: relative;
}

.fluid-img {
    height: 100%;
    object-fit: cover;
}

.hero-img-gradient {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,.4);
    background-image: linear-gradient(to top,rgba(0,0,0,.8) 0,rgba(0,0,0,0) 60%,rgba(0,0,0,.8) 100%);
}

.hero-content-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: white;
    max-width: 50em;
    margin: 0 auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1em;
}

.welcome-text {
    font-size: 1.75rem;
    font-weight: 600;
}

.hero-title {
    font-size: 4rem;
}

button {
    background-color: #e50914;
    color: white;
    border: 0;
    cursor: pointer;
    padding: .85rem 3rem;
    font-size: 1.45rem;
    transition: 0.2s;
    border-radius: 1px;
}

button:hover {
    transform: translateY(-2px);
    opacity: 0.9;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
}

.logo {
    padding: 0.5rem 2rem;
}

.logo > img {
    max-width: 13em;
}

@media screen and (max-width: 767px) {
    .welcome-text {
        font-size: 1.45rem;
        font-weight: 600;
    }
    
    .hero-title {
        font-size: 2.85rem;
    }
    
    button {
        padding: .8rem 2.85rem;
        font-size: 1.25rem;
    }
}

@media screen and (max-width: 500px) {
    .welcome-text {
        font-size: 1rem;
        font-weight: 600;
    }
    
    .hero-title {
        font-size: 1.85rem;
    }
    
    button {
        padding: .6rem 2.25rem;
        font-size: 1rem;
    }
}